<template lang="pug">
	.send
		.frame
			.border
				.top
				.bottom
				
			.header
				.align
				.user
					.photo
						img(:src="currenUser.photo")
					span.name {{currenUser.name}}
				.themes
			
			.wrap-input
				//- .input(contenteditable, data-placeholder="teste")
				contenteditable(
					class="input",
					tag="p",
					:contenteditable="true", 
					v-model="message", 
					:noNL="true", 
					:noHTML="true")

				button.button-send(@click="send") Enviar

			.footer
				.fonts
					span.font.active Aa
					span.font Aa
					span.font Aa
					
</template>
<script>
import Vue from 'vue'
import { doc, updateDoc, arrayUnion, collection, query, where, getFirestore, getDocs } from "firebase/firestore";
import contenteditable from 'vue-contenteditable'
Vue.use(contenteditable)

export default {
	name: 'SendView',
	data() {
		return{
			message: '',
			currenUserRef: '',
			currenUser: ''
		}
	},
	created(){
		this.fetchUser();
	},
	beforeRouteEnter (to, from, next) {
		if(to.params.username){
			next()
		}else{
			next('/');
		}
	},
	components: {
	},
	methods: {
		async fetchUser(){
			const db = getFirestore();
			const usersRef = collection(db, 'users');
			// Create a query against the collection.
			const q = query(usersRef, where('username', '==', this.$route.params.username));

			const querySnapshot = await getDocs(q);
			querySnapshot.forEach((doc) => {
				this.currenUserRef = doc.id;
				this.currenUser = doc.data();
			});
		},
		clearData(){
			this.message = '';
		},
		async send(){
			const db = getFirestore();
			const user = doc(db, 'users', this.currenUserRef);
			
			await updateDoc(user, {
				messages: arrayUnion({text: this.message, emoji: 'oioi'})
			});
			
			this.clearData();
		}
	}
}
</script>
<style lang="stylus" src="@/assets/styles/views/Send.styl"></style>